@-webkit-keyframes opacityanimate{from{background-color:#e4e4e4}to{background-color:#c7c7c7}}@keyframes opacityanimate{from{background-color:#e4e4e4}to{background-color:#c7c7c7}}.header-container{padding:15px 45px;background:#10101a;background:linear-gradient(0deg, #071622 0%, #15151f 100%, rgba(61,162,183,0) 100%);display:flow-root;box-shadow:0 19px 20px #333,0 10px 12px #0e1621}.header-menu-container{align-items:center}.header-menu-container .header-menu{float:left;margin-left:45px;margin-right:45px;margin-top:5px;color:#5f8d71;transition:color 1s;cursor:pointer}.header-menu-container :hover{color:#17b353}.header-menu-container .header-menu-active{color:#007a2f}.header-logo{float:left;padding:15px;background-color:#fff}.header-logo .logo-title{color:#007a2f !important;font-size:18px;margin:0 auto}.link-container{display:inline-flex;margin:5px;position:relative}

